.game-settings {
    background: #1E1E1E;
    color: #F5F5F5 !important;
    width: 300px;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    padding: 30px;
    font-weight: bold;
    user-select: none;
    display: flex;
    flex-direction: column;
    margin-right: 100px;
}
.game-settings label {
    display: block;
    margin-bottom: 5px;
}
.game-settings > div:nth-child(2) {
    margin-bottom: 15px;
}
.game-settings > div > div {
    margin-bottom: 15px;
}
.game-settings input {
    width: calc(100% - 24px);
    height: 32px;
    padding: 0px 10px;
    background: #0F1011;
    border: 2px solid #242D33;
    border-radius: 4px;
    color: white;
    font-weight: bold;
}
.game-settings select {
    width: 100%;
    height: 36px;
    padding: 0px 8px;
    background: #0F1011;
    border: 2px solid #242D33;
    border-radius: 4px;
    cursor: pointer;
    color: white;
    font-weight: bold;
}
.game-settings .game-bet-btn, .game-settings .game-close-btn {
    background-color: #FABC51;
    border: none;
    outline: none;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 1.125em;
    padding: 0.555em;
    border-radius: 4px;
    cursor: pointer;
    box-shadow: 0 0.2em 0 0 #7E6200;
    transition: all .3s ease;
    color: #000;
    width: 100%;
    height: 50px;
}
.game-settings .game-bet-btn:hover:enabled, .game-settings .game-close-btn:hover:enabled {
    -webkit-transform: scale(1.03);
    transform: scale(1.03);
}
.game-settings .game-bet-btn:active, .game-settings .game-close-btn:active {
    box-shadow: 0 0 0 0 #9A8401;
    -webkit-transform: scale(.95) !important;
    transform: scale(.95) !important;
}
.betamount-subbtn {
    margin-top: 3px;
    padding: 0px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.betamount-subbtn span {
    cursor: pointer;
    font-size: 12px;
    user-select: none;
}
.betamount-subbtn span.disable{
    cursor: default;
    color: #77767A;
}
.game-settings .game-risk-btn-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.game-risk-btn-container .game-risk-btn {
    width: 32%;
    border: none;
    outline: none;
    color: black;
    font-weight: 900;
    font-size: 1em;
    cursor: pointer;
    background-color: #999999;
    box-shadow: 0 0.2em 0 0 #6A6666;
    padding: 0.625em;
    border-radius: 0.3125em;
    transition: all .3s ease;
}
.game-risk-btn-container .game-risk-btn:hover:enabled {
    -webkit-transform: scale(1.03);
    transform: scale(1.03);
}
.game-risk-btn-container .game-risk-btn:hover:enabled {
    background-color: #FFBE50;
    box-shadow: 0 0.2em 0 0 #7E6200;
}
.game-risk-btn-container .game-risk-btn-active, .game-risk-btn-active:hover:enabled {
    background-color: #FFBE50;
    box-shadow: 0 0.2em 0 0 #7E6200;
}
.game-rows-slider {
    height: 0px !important;
    padding: 0px !important;
    width: calc(100% - 10px) !important;
    border: 1px solid !important;
}
.game-animation-toggle input[type=checkbox]{
    height: 0;
    width: 0;
    visibility: hidden;
}
.game-animation-toggle label {
    cursor: pointer;
    text-indent: -9999px;
    width: 100%;
    height: 50px;
    background: grey;
    display: block;
    border-radius: 100px;
    position: relative;
}
.game-animation-toggle label:after {
    content: '';
    position: absolute;
    top: 5px;
    left: 5px;
    width: 45%;
    height: 40px;
    background: #fff;
    border-radius: 90px;
    transition: 0.3s;
}
.game-animation-toggle input:checked + label {
    background: #FABC51;
}
.game-animation-toggle input:checked + label:after {
    left: calc(100% - 5px);
    transform: translateX(-100%);
}
.game-animation-toggle .game-animation-toggle label:active:after {
    width: 40%;
}
.game-settings .game-animation-toggle-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin: 30px 0px 30px;
}
.game-settings .game-animation-toggle-container div {
    width: 150px;
    margin-top: -18px;
}
.game-settings .game-animation-toggle-container input {
    margin: 0px;
    padding: 0px;
}
.game-settings .game-animation-toggle-container label {
    margin: 0px;
}
.rows-label {
    display: flex;
    align-items: center;
    justify-content: space-between;
}