body {
  background: #121212;
}
#root {
  display: flex;
  justify-content: center;
}
.plinko-board {
  display: flex;
  justify-content: center;
  margin-top: 75px;
  margin-bottom: 75px;
}
@media only screen and (max-width: 1366px) {
  .plinko-board {
    flex-direction: column-reverse;
    max-width: 700px;
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .game-settings {
    margin-right: 0px;
  }
  .p5Canvas {
    width: 100% !important;
    height: 100% !important;
  }
  .game-settings {
    width: initial;
    border-top-left-radius: 0px;
    border-bottom-right-radius: 10px;
    flex-direction: column-reverse;
  }
  .game-board {
    border-top-left-radius: 10px;
    border-bottom-right-radius: 0px !important;
  }
  .payout-group {
    margin-bottom: 18px;
  }
  .payout-btn {
    height: 30px;
    font-size: 12px;
  }
  .payout-btn span {
    padding-bottom: 1px;
  }
}
@media only screen and (max-width: 920px) {
  .plinko-board {
    flex-direction: column-reverse;
    max-width: 500px;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 30px;
  }
  .p5Canvas {
    width: 100% !important;
    height: 100% !important;
  }
  .game-settings {
    width: initial;
    border-top-left-radius: 0px;
    border-bottom-right-radius: 10px;
    flex-direction: column-reverse;
  }
  .game-board {
    border-top-left-radius: 10px;
    border-bottom-right-radius: 0px !important;
  }
  .payout-group {
    margin-bottom: 18px;
  }
  .payout-btn {
    height: 16px;
    font-size: 8px;
  }
  .payout-btn span {
    padding-bottom: 1px;
  }
}
@media only screen and (max-width: 576px) {
  .plinko-board {
    margin-top: 20px;
    margin-bottom: 20px;
  }
  .modal-content .multiplier-details {
    display: grid;
    grid-template-columns: auto auto auto auto auto auto;
    row-gap: 20px;
    justify-content: center;
  }
}
@media only screen and (max-width: 360px) {
  .modal-content .multiplier-details {
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    row-gap: 10px;
    justify-content: center;
  }
}