.game-result {
    width: inherit !important;
    padding: inherit !important;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(2, 2, 2, 0.7);
}  
.modal-main {
    position:fixed;
    background: #1E1E1E;
    max-width: 500px;
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    padding: 20px;
    border-radius: 8px;
}
.modal-main .modal-header, .modal-main .modal-content {
    margin-bottom: 30px;
}
.modal-main .modal-content {
    max-height: 500px;
    overflow-y: auto;
    padding: 10px;
    box-sizing: border-box;
}
.modal-main .modal-content::-webkit-scrollbar {
    width: 6px;
}
.modal-main .modal-content::-webkit-scrollbar-track-piece {
    background: #888;
    border-radius: 10px;
}
.modal-main .modal-content::-webkit-scrollbar-thumb {
    background: #FABC51;
    border-radius: 10px;
}
.modal-main .modal-content > div {
    margin-bottom: 10px;
}
.modal-content .bet-results {
    background: #0F1011;
    border: 2px solid #242D33;
    padding: 20px 0px;
    border-radius: 4px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 14px;
}
.modal-content .bet-results, .modal-content .multiplier-details {
    margin-bottom: 20px !important;
}
.modal-content .bet-results span {
    display: block;
}
.modal-content .bet-results img {
    margin-left: 5px;
}
.modal-content .bet-results div span:nth-child(1) {
    margin-bottom: 10px;
}
.modal-content .bet-results div span:nth-child(2) {
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
}
.modal-content .multiplier-details {
    display: grid;
    grid-template-columns: auto auto auto auto auto auto auto auto;
    row-gap: 20px;
    justify-content: center;
}
.modal-content .multiplier-details div {
    --shadow-color: #612D5A;
    border-radius: 0.3em;
    text-align: center;

    -webkit-animation-duration: .1s !important;
    animation-duration: .1s !important;
    -webkit-animation-timing-function: cubic-bezier(.18,.89,.32,1.28);
    animation-timing-function: cubic-bezier(.18,.89,.32,1.28);
    
    box-shadow: 0 0.2em 0 0 var(--shadow-color);
    height: 28px;
    font: inherit;
    font-family: sans-serif;
    font-weight: 700;
    font-size: 14px;
    color: #000c;
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 40px;
    margin: 0px 5px;
}
.display-block {
    display: block;
}  
.display-none {
    display: none;
}